<template>
  <div class="attendance">
    <h4></h4>
    <el-table :data="shifts" border>
      <el-table-column prop="shift.dayOfTheWeek" label="" width="50"></el-table-column>
      <el-table-column prop="shift.startTime" label="" width="100"></el-table-column>
      <el-table-column prop="shift.endTime" label="" width="100"></el-table-column>
      <el-table-column prop="shift.location" label="" width="120"></el-table-column>
      <el-table-column prop="shift.hourlyPay" label="" width="50"></el-table-column>
      <el-table-column prop="startDate" label="" width="100"></el-table-column>
      <el-table-column prop="endDate" label="" width="100"></el-table-column>
    </el-table>
    <el-button type="primary" icon="el-icon-edit-outline" @click="signIn()"></el-button>
    <el-button type="primary" icon="el-icon-document-checked" @click="signOut()"></el-button>
  </div>
</template>

<script>
var dayOfTheWeek = ['', '', '', '', '', '', '']
export default {
  name: 'Attendance',
  data: function() {
    return {
      shifts: null,
    }
  },
  created: function() {
    this.axios({
      method: "get",
      url: "/librarian/view-own-regular-shifts",
    })
    .then(response => {
      if (response.status == 200) {
        if (response.data.code == 0) {
          this.shifts = response.data.data;
          for (var i = 0; i < this.shifts.length; i++) {
            this.shifts[i].shift.dayOfTheWeek = dayOfTheWeek[this.shifts[i].shift.dayOfTheWeek - 1]
          }
        } else {
          this.$message.error(response.data.message);
        }
      } else {
        this.$message.error("");
      }
    });
  },
  methods: {
    signIn() {
      this.axios({
        method: "get",
        url: "/librarian/sign-in",
      })
      .then(response => {
        if (response.status == 200) {
          if (response.data.code == 0) {
            this.$message.success("");
          } else {
            this.$message.error(response.data.message);
          }
        } else {
          this.$message.error("");
        }
      });
    },
    signOut() {
      this.axios({
        method: "get",
        url: "/librarian/sign-out",
      })
      .then(response => {
        if (response.status == 200) {
          if (response.data.code == 0) {
            this.$message.success("");
          } else {
            this.$message.error(response.data.message);
          }
        } else {
          this.$message.error("");
        }
      });
    }
  }
}
</script>
